"use client"

import { useEffect, useState, useRef } from "react"
import { useParams } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ChatBubble } from "@/components/chat-bubble"
import { MessageInput } from "@/components/message-input"
import { TypingIndicator } from "@/components/typing-indicator"
import { motion } from "framer-motion"
import { storage } from "@/lib/storage"
import { db } from "@/lib/supabase/db"
import { createClient } from "@/lib/supabase/client"
import type { Business, Conversation, Message } from "@/lib/types"
import { v4 as uuidv4 } from "uuid"
import { AlertCircle } from "lucide-react"

export default function CustomerChatPage() {
  const params = useParams()
  const phone = params.phone as string

  const [business, setBusiness] = useState<Business | null>(null)
  const [conversation, setConversation] = useState<Conversation | null>(null)
  const [customerEmail, setCustomerEmail] = useState("")
  const [showEmailPrompt, setShowEmailPrompt] = useState(true)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const typingTimeoutRef = useRef<NodeJS.Timeout | null>(null)
  const supabase = createClient()

  useEffect(() => {
    const loadBusiness = async () => {
      const biz = await storage.getBusinessByPhone(phone)
      if (!biz) {
        setError("Business not found")
        setLoading(false)
        return
      }
      setBusiness(biz)
      setLoading(false)
    }
    loadBusiness()
  }, [phone])

  const handleStartChat = async (email?: string) => {
    if (!business) return

    // Try to find existing conversation by customer phone
    const existingConversation = await db.getConversationByBusinessAndPhone(business.id, phone)

    if (existingConversation) {
      // Update email if provided and different
      if (email && existingConversation.customerEmail !== email) {
        const updated = await storage.updateConversation(existingConversation.id, {
          customerEmail: email,
        })
        if (updated) {
          setConversation(updated)
        }
      } else {
        setConversation(existingConversation)
      }
    } else {
      const newConversation: Conversation = {
        id: uuidv4(),
        businessId: business.id,
        customerEmail: email || undefined,
        createdAt: new Date().toISOString(),
        lastMessageAt: new Date().toISOString(),
        messages: [],
      }
      await storage.createConversation(newConversation, phone)
      const created = await storage.getConversationById(newConversation.id)
      if (created) {
        setConversation(created)
      }
    }

    if (email) {
      setCustomerEmail(email)
    }
    setShowEmailPrompt(false)
  }

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [conversation?.messages])

  // Set up real-time subscription when conversation is loaded
  useEffect(() => {
    if (!conversation?.id) return

    const channel = supabase
      .channel(`conversation:${conversation.id}`)
      .on(
        "postgres_changes",
        {
          event: "INSERT",
          schema: "public",
          table: "messages",
          filter: `conversation_id=eq.${conversation.id}`,
        },
        async (payload) => {
          const updated = await storage.getConversationById(conversation.id)
          if (updated) {
            setConversation(updated)
          }
        }
      )
      .subscribe()

    return () => {
      supabase.removeChannel(channel)
    }
  }, [conversation?.id, supabase])

  const handleSendMessage = async (text: string) => {
    if (!conversation) return
    if (isSending) return

    setIsSending(true)

    const tempId = uuidv4()
    const newMessage: Message = {
      id: tempId,
      conversationId: conversation.id,
      senderType: "customer",
      senderId: "customer",
      text,
      status: "sent",
      createdAt: new Date().toISOString(),
    }

    // Optimistically update UI immediately
    const optimisticConversation: Conversation = {
      ...conversation,
      messages: [...conversation.messages, newMessage],
      lastMessageAt: new Date().toISOString(),
    }
    setConversation(optimisticConversation)
    setIsTyping(false)

    try {
      await db.createMessage(newMessage)
      const updated = await storage.getConversationById(conversation.id)
      if (updated) {
        setConversation(updated)
      }
    } catch (error) {
      console.error("Failed to send message:", error)
      setConversation(conversation)
    } finally {
      setIsSending(false)
    }
  }

  const handleSendImage = async (imageUrl: string) => {
    if (!conversation) return
    if (isSending) return

    setIsSending(true)

    const tempId = uuidv4()
    const newMessage: Message = {
      id: tempId,
      conversationId: conversation.id,
      senderType: "customer",
      senderId: "customer",
      imageUrl,
      status: "sent",
      createdAt: new Date().toISOString(),
    }

    // Optimistically update UI immediately
    const optimisticConversation: Conversation = {
      ...conversation,
      messages: [...conversation.messages, newMessage],
      lastMessageAt: new Date().toISOString(),
    }
    setConversation(optimisticConversation)
    setIsTyping(false)

    try {
      await db.createMessage(newMessage)
      const updated = await storage.getConversationById(conversation.id)
      if (updated) {
        setConversation(updated)
      }
    } catch (error) {
      console.error("Failed to send image:", error)
      setConversation(conversation)
    } finally {
      setIsSending(false)
    }
  }

  const handleTyping = () => {
    setIsTyping(true)
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current)
    }
    typingTimeoutRef.current = setTimeout(() => {
      setIsTyping(false)
    }, 3000)
  }

  if (loading) {
    return (
      <main className="h-screen flex items-center justify-center bg-background">
        <p className="text-muted-foreground">Loading...</p>
      </main>
    )
  }

  if (error || !business) {
    return (
      <main className="h-screen flex items-center justify-center bg-background p-4">
        <div className="text-center">
          <AlertCircle className="w-12 h-12 text-destructive mx-auto mb-4" />
          <h1 className="text-2xl font-bold mb-2">Error</h1>
          <p className="text-muted-foreground">{error}</p>
        </div>
      </main>
    )
  }

  if (showEmailPrompt) {
    return (
      <main className="h-screen flex items-center justify-center bg-gradient-to-br from-background via-card to-background p-4">
        <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} className="w-full max-w-md">
          <div className="bg-card rounded-xl border border-border p-8 shadow-lg text-center">
            <h1 className="text-3xl font-bold text-primary mb-2">Leenk</h1>
            <h2 className="text-2xl font-bold mb-2">{business.businessName}</h2>
            <p className="text-muted-foreground mb-8">{business.online ? "ðŸŸ¢ Online" : "ðŸ”´ Offline"}</p>

            <form
              onSubmit={(e) => {
                e.preventDefault()
                handleStartChat(customerEmail)
              }}
              className="space-y-4"
            >
              <div>
                <label className="block text-sm font-medium mb-2">Email (optional)</label>
                <input
                  type="email"
                  value={customerEmail}
                  onChange={(e) => setCustomerEmail(e.target.value)}
                  placeholder="your@email.com"
                  className="w-full bg-input border border-border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary"
                />
              </div>

              <Button type="submit" className="w-full">
                Start Chat
              </Button>

              <button
                type="button"
                onClick={() => handleStartChat()}
                className="w-full text-sm text-muted-foreground hover:text-foreground"
              >
                Continue without email
              </button>
            </form>
          </div>
        </motion.div>
      </main>
    )
  }

  return (
    <main className="h-screen flex flex-col bg-background">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        className="border-b border-border bg-card p-4"
      >
        <h2 className="font-semibold text-lg">{business.businessName}</h2>
        <p className="text-xs text-muted-foreground">{business.online ? "ðŸŸ¢ Online" : "ðŸ”´ Offline"}</p>
      </motion.div>

      {/* Messages */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="flex-1 overflow-y-auto p-4 space-y-2 bg-gradient-to-b from-background to-card/30"
      >
        {conversation.messages.length === 0 ? (
          <div className="flex items-center justify-center h-full text-muted-foreground text-center">
            <p>Start a conversation with {business.businessName}</p>
          </div>
        ) : (
          conversation.messages.map((msg, idx) => (
            <ChatBubble key={msg.id} message={msg} isOwn={msg.senderType === "customer"} index={idx} />
          ))
        )}
        <div ref={messagesEndRef} />
      </motion.div>

      {/* Message Input */}
      <MessageInput onSendMessage={handleSendMessage} onSendImage={handleSendImage} />
    </main>
  )
}
